'use-client'
import { useState } from 'react';
import { FaMapMarkerAlt, FaClock, FaCalendarAlt } from 'react-icons/fa';

const PickupRequestForm = () => {
  const [formData, setFormData] = useState({
    name: '',
    pinCode: '',
    city: '',
    locality: '',
    landmark: '',
    address: '',
    propertyType: '',
    alternateMobile: '',
    items: '',
    pickupDate: '',
    preferredTime: '',
    exceptionalDay: ''
  });

  const handleChange = (e) => {
    const { name, value } = e.target;
    setFormData(prevState => ({
      ...prevState,
      [name]: value
    }));
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    // Form submission logic here
    console.log(formData);
  };

  return (
    <div className="max-w-md mx-auto bg-white p-6 rounded-md shadow-sm">
      <div className="bg-black text-white p-4 -mx-6 -mt-6 mb-6 flex items-center">
        <div className="mr-4">
          <svg xmlns="http://www.w3.org/2000/svg" className="h-6 w-6" fill="none" viewBox="0 0 24 24" stroke="currentColor">
            <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M9 12h6m-6 4h6m2 5H7a2 2 0 01-2-2V5a2 2 0 012-2h5.586a1 1 0 01.707.293l5.414 5.414a1 1 0 01.293.707V19a2 2 0 01-2 2z" />
          </svg>
        </div>
        <h2 className="text-xl font-semibold">New Pickup Request</h2>
      </div>

      <form onSubmit={handleSubmit}>
        <div className="flex mb-4">
          <div className="bg-green-500 text-white p-3 flex items-center justify-center">
            <FaMapMarkerAlt />
          </div>
          <input
            type="text"
            name="name"
            value={formData.name}
            onChange={handleChange}
            placeholder="Please enter name"
            className="flex-grow p-2 border border-gray-300 focus:outline-none focus:ring-1 focus:ring-green-500"
            required
          />
        </div>

        <div className="flex mb-4">
          <div className="bg-green-500 text-white p-3 flex items-center justify-center">
            <FaMapMarkerAlt />
          </div>
          <input
            type="text"
            name="pinCode"
            value={formData.pinCode}
            onChange={handleChange}
            placeholder="Area Pin Code"
            className="flex-grow p-2 border border-gray-300 focus:outline-none focus:ring-1 focus:ring-green-500"
            required
          />
        </div>

        <div className="mb-4">
          <label className="block text-gray-700 mb-2">Address</label>
          <select
            name="city"
            value={formData.city}
            onChange={handleChange}
            className="w-full p-2 border border-gray-300 mb-3 focus:outline-none focus:ring-1 focus:ring-green-500"
            required
          >
            <option value="">City</option>
            <option value="delhi">Delhi</option>
            <option value="mumbai">Mumbai</option>
            <option value="bangalore">Bangalore</option>
            <option value="hyderabad">Hyderabad</option>
          </select>

          <select
            name="locality"
            value={formData.locality}
            onChange={handleChange}
            className="w-full p-2 border border-gray-300 mb-3 focus:outline-none focus:ring-1 focus:ring-green-500"
            required
          >
            <option value="">Locality</option>
            <option value="locality1">Locality 1</option>
            <option value="locality2">Locality 2</option>
            <option value="locality3">Locality 3</option>
          </select>

          <input
            type="text"
            name="landmark"
            value={formData.landmark}
            onChange={handleChange}
            placeholder="Landmark"
            className="w-full p-2 border border-gray-300 mb-3 focus:outline-none focus:ring-1 focus:ring-green-500"
          />

          <textarea
            name="address"
            value={formData.address}
            onChange={handleChange}
            placeholder="address"
            className="w-full p-2 border border-gray-300 mb-3 focus:outline-none focus:ring-1 focus:ring-green-500"
            rows="2"
            required
          ></textarea>

          <select
            name="propertyType"
            value={formData.propertyType}
            onChange={handleChange}
            className="w-full p-2 border border-gray-300 mb-3 focus:outline-none focus:ring-1 focus:ring-green-500"
            required
          >
            <option value="">--Select--</option>
            <option value="house">House</option>
            <option value="apartment">Apartment</option>
            <option value="office">Office</option>
            <option value="other">Other</option>
          </select>

          <input
            type="tel"
            name="alternateMobile"
            value={formData.alternateMobile}
            onChange={handleChange}
            placeholder="Enter alternate mobile"
            className="w-full p-2 border border-gray-300 mb-3 focus:outline-none focus:ring-1 focus:ring-green-500"
          />

          <textarea
            name="items"
            value={formData.items}
            onChange={handleChange}
            placeholder="What are items you have??"
            className="w-full p-2 border border-gray-300 mb-3 focus:outline-none focus:ring-1 focus:ring-green-500"
            rows="2"
            required
          ></textarea>

          <input
            type="date"
            name="pickupDate"
            value={formData.pickupDate}
            onChange={handleChange}
            placeholder="Please select the pickupdate"
            className="w-full p-2 border border-gray-300 mb-3 focus:outline-none focus:ring-1 focus:ring-green-500"
            required
          />

          {/* Additional Field: Preferred Time */}
          <div className="flex mb-3">
            <div className="bg-green-500 text-white p-3 flex items-center justify-center">
              <FaClock />
            </div>
            <select
              name="preferredTime"
              value={formData.preferredTime}
              onChange={handleChange}
              className="flex-grow p-2 border border-gray-300 focus:outline-none focus:ring-1 focus:ring-green-500"
              required
            >
              <option value="">Select Preferred Time</option>
              <option value="morning">Morning (8 AM - 12 PM)</option>
              <option value="afternoon">Afternoon (12 PM - 4 PM)</option>
              <option value="evening">Evening (4 PM - 8 PM)</option>
            </select>
          </div>

          {/* Additional Field: Exceptional Day (not required) */}
          <div className="flex mb-3">
            <div className="bg-green-500 text-white p-3 flex items-center justify-center">
              <FaCalendarAlt />
            </div>
            <select
              name="exceptionalDay"
              value={formData.exceptionalDay}
              onChange={handleChange}
              className="flex-grow p-2 border border-gray-300 focus:outline-none focus:ring-1 focus:ring-green-500"
            >
              <option value="">Select Unavailable Day (Optional)</option>
              <option value="monday">Monday</option>
              <option value="tuesday">Tuesday</option>
              <option value="wednesday">Wednesday</option>
              <option value="thursday">Thursday</option>
              <option value="friday">Friday</option>
              <option value="saturday">Saturday</option>
              <option value="sunday">Sunday</option>
            </select>
          </div>

          <button
            type="submit"
            className="w-full bg-green-500 hover:bg-green-600 text-white py-3 rounded-full text-lg font-medium transition-colors"
          >
            Submit
          </button>
        </div>
      </form>
    </div>
  );
};